export default{
  data () {
    return {
      goodsDetail: {},
      // 默认选中菜单是图文介绍
      activeIndex: 0,
      menuArr: [
        '图文介绍',
        '规格参数'
      ]
    }
  },
  onLoad (options) {
    console.log(options.goodsId)
    this.getGoodsDetail(options.goodsId)
  },
  /*
    title标题
    imageUrl分享的图片
  */
  onShareAppMessage () {
    return {
      title: this.goodsDetail.goods_name,
      imageUrl: this.goodsDetail.pics[0].pics_big
    }
  },
  methods: {
		share(){
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 1,
			    summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
		},
    // 跳转到pay页面，并传递goodsId
    buy () {
      wx.navigateTo({ url: '/pages/pay/main?goodsId=' + this.goodsDetail.goods_id })
    },
    // 添加购物车
    add2Cart () {
      // 改变state.cart
      this.$store.commit('add2Cart', this.goodsDetail.goods_id)
    },
    toCart () {
      wx.switchTab({ url: '/pages/cart/main' })
    },
    /*
    预览图片
    urls：需要预览的图片http链接列表
    */
    previewImg (index) {
      let urls = []
      this.goodsDetail.pics.forEach(v => {
        urls.push(v.pics_big)
      })
      wx.previewImage({
        current: urls[index], // 当前显示图片的http链接
        urls
      })
    },
    getGoodsDetail (goodsId) {
      this.$request({
        url: `/api/public/v1/goods/detail?goods_id=${goodsId}`
      }).then(data => {
        console.log(data)
        this.goodsDetail = data
      })
    }
  }
}
